html {
  height: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "PTMono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: black;
  background: white;
  overflow-x: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

.wrapper {
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow-x: hidden;
}

main {
  flex-grow: 1;
}

.container {
  padding: 0 calc(50% - 660px);
}

.header {
  margin-bottom: 20px;
}
.header .container {
  padding: 0 calc(50% - 236px);
}

.header-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-nav {
  padding: 20px 20px;
}

.header-nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  min-height: 110px;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.header-nav-item:not(:last-child) {
  margin-bottom: 15px;
}

.header-nav-link {
  display: block;
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
}
.header-nav-link:hover {
  transform: scale(1.2);
  transition: 0.2s;
  border-bottom: 1px solid orange;
}
.header-nav-link:focus {
  transform: scale(1.2);
  transition: 0.2s;
  border-bottom: 1px solid orange;
}
.header-nav-link:active {
  transform: scale(1);
  transition: 0.2s;
}

.intro {
  background: white url("../img/bg/bg-main.png") no-repeat center;
  background-size: cover;
  min-height: 515px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 80px;
}
@media (min-resolution: 2dppx) {
  .intro {
    background: white url("../img/bg/bg-main@2x.png") no-repeat center;
    background-size: cover;
  }
}

.intro h2 {
  margin: 0;
  padding: 0;
  font-size: 32px;
  font-weight: 700;
  line-height: 39px;
  letter-spacing: 0.01em;
  margin-bottom: 50px;
  color: black;
  text-align: center;
}

.intro-link {
  font-size: 32px;
  font-weight: 700;
  letter-spacing: 0.01em;
  background: #f8cc32;
  padding: 35px 8%;
}
.intro-link:hover {
  transform: scale(0.9);
  transition: 0.3s;
}
.intro-link:focus {
  transform: scale(0.9);
  transition: 0.3s;
}
.intro-link:active {
  transform: scale(0.9);
  transition: 0.3s;
}

.about {
  margin-bottom: 80px;
}

.swiper-wrapper {
  text-align: center;
}

.about p {
  text-align: center;
  font-size: 20px;
  font-weight: 500;
  line-height: 32px;
}
.about p:last-of-type {
  margin-bottom: 50px;
}

.swiper-slide img {
  object-fit: cover;
  height: 212px;
}

footer {
  padding-bottom: 50px;
}

.service {
  margin-bottom: 80px;
}

.service h2 {
  margin: 0;
  padding: 0;
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 50px;
  background: #f8cc32;
  padding: 30px 0;
}

.service h3 {
  margin: 0;
  padding: 0;
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  text-transform: uppercase;
  padding: 20px 0;
  cursor: pointer;
  position: relative;
}
.service h3::after {
  position: absolute;
  content: "";
  width: 15px;
  height: 15px;
  top: 23px;
  right: 30px;
  background: url("../img/icon/plus.svg");
}
.service h3:hover {
  background: #f8cc32;
}

.service-wrapper {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 0 calc(50% - 400px);
}

.service-list {
  margin: 0;
  padding: 0;
  display: none;
  padding: 25px 25px 25px 40px;
  border: 1px solid black;
  flex-direction: row;
  flex-wrap: wrap;
}

.service-list__item {
  margin-bottom: 10px;
  font-size: 18px;
  line-height: 32px;
  flex: 1 1 50%;
}

.service--nojs .service-list {
  display: flex;
}

.service-list--active {
  display: flex;
}

.service-block--active h3 {
  background: #f8cc32;
}
.service-block--active h3:after {
  background: url("../img/icon/minus.svg");
}

@media (max-width: 767px) {
  .service-wrapper {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }

  .service-list {
    padding-left: 50px;
    border: none;
  }

  .service-list__item {
    flex: 1 1 100%;
  }
}
.footer h2 {
  margin: 0;
  padding: 0;
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 50px;
  background: #f8cc32;
  padding: 30px 0;
}

.footer-nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-around;
}

.footer-nav-link {
  display: block;
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
}
.footer-nav-link:hover {
  transform: scale(1.2);
  transition: 0.2s;
  border-bottom: 1px solid orange;
}
.footer-nav-link:focus {
  transform: scale(1.2);
  transition: 0.2s;
  border-bottom: 1px solid orange;
}
.footer-nav-link:active {
  transform: scale(1);
  transition: 0.2s;
}